<template>
    <div>
        <div class="">
            <div v-for="(v, o) in list" :key="o" class="list flexBox cloc">
                <!-- <div class="mr10">
                    <img :src='require("@/assets/img/user.png")' class="" style="width:30px;height:30px;" />
                </div> -->
                <div class=" pb5" style="flex:1;border-bottom: 1px solid #333;">
                    <!-- <div class="flexBox" style="line-height: 30px;">
                        <span class="text_deal" style="flex:1;">{{ v.nickname }}</span>
                        <img :src='require("@/assets/img/reply.svg")' @click="form.parentId=v.userId;isSendPop=true;" class="active_t" style="width:18px;height:18px;" />
                    </div> -->
                    <div class="mt5 text_deal">{{ v.content||'没有内容...' }}</div>
                    <div class="mt5">{{ v.nickname }}<span class="ml5">{{ v.time||'没有内容...' }}</span></div>
                </div>
            </div>
        </div>
        <div class="mt20">
            <Page :total="page.totalCount" :current="page.current" @on-change="handlePage" show-total />
        </div>
    </div>
</template>

<script>
export default {
    name: 'WebNews',
    data() {
        return {
            list:[],
            page:{
                "type": '',
                "current": 1,
                "size": 8,
                totalCount:0,
            },
        };
    },
    mounted() {
        this.getList();
    },
    methods: {
        handlePage(page) {
            this.page.current = page;
            this.getList();
        },
        toDetail(v) {
            localStorage.setItem('comment_detail', JSON.stringify(v));
            this.$router.push(`/commentDetail`);
        },
        //根据接口获取
        async getList(){
            let d={
                // 'area':'shenzhen',
                "current": this.page.current,
                "size": this.page.size,
            };
            let res = await this.$api.trading_view_com_oneself(d);
            let list = res.list || [];
            this.list=list;
            this.page.totalCount= res.totalCount;
        },
    },
};
</script>

<style lang="less" scoped>
.dataLIst{
    padding:10px 5px;
}
.dataLIst:hover{
    background: rgba(56, 97, 251, 0.1);
}
</style>