                                }], false),
                                // shadowColor: 'rgba(156, 22, 246,1)', //阴影颜色
                                // shadowBlur: 20 //shadowBlur设图形阴影的模糊大小。配合shadowColor,shadowOffsetX/Y, 设置图形的阴影效果。
                            }
                        },
                        smooth: true,
                        itemStyle: {
                            normal: { 
                                //颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
                                /**
                                *   a ,b,c,d为0，1
                                *   a:1 arr中的颜色右到左
                                *   c:1 arr中的颜色左到右
                                *   b:1 arr中的颜色下到上
                                *   d:1 arr中的颜色上到下
                                **/
                                color: new echarts.graphic.LinearGradient(0, 0, 1, 0, [{
