<template>
    <div>
        <div class="">
            <div v-for="(v, o) in list" :key="o" @click="toDetail(v)" class="dataLIst cloc7">
                <div class="">{{ v.title || '无标题' }}</div>
                <div class="alignCenter f12 mt5 op7">
                    <div class="text_deal" style="flex:1;">
                        <span>{{ v.nickname }}</span>
                        <!-- <span class="ml10">#重要舆论#</span> -->
                    </div>
                    <div class="ml10">{{ v.time | getDateDiff }}</div>
                </div>
            </div>
        </div>
        <div class="mt20">
            <Page :total="page.totalCount" :current="page.current" @on-change="handlePage" show-total />
        </div>
    </div>
</template>

<script>
export default {
    name: 'WebNews',
    data() {
        return {
            list:[],
            page:{
                "type": '',
                "current": 1,
                "size": 8,
                totalCount:0,
            },
        };
    },
    mounted() {
        this.getList();
    },
    methods: {
        handlePage(page) {
            this.page.current = page;
            this.getList();
        },
        toDetail(v) {
            localStorage.setItem('comment_detail', JSON.stringify(v));
            this.$router.push(`/commentDetail`);
        },
        //根据接口获取
        async getList(){
            let d={
                'area':'',
                "current": this.page.current,
                "size": this.page.size,
            };
            let res = await this.$api.point_viewpoint_page(d);
            let list = res.list || [];
            this.list=list;
            this.page.totalCount= res.totalCount;
        },
    },
};
</script>

<style lang="less" scoped>
.dataLIst{
    padding:10px 5px;
}
.dataLIst:hover{
    background: rgba(56, 97, 251, 0.1);
}
</style>